import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.min.css'

//Define Square click function
function Square(props) {
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}
// Set defaultState
const defaultState = {
  squares: Array(9).fill(null),
    xIsNext: true,
};
//Define Board class with squares
class Board extends React.Component {
  constructor(props){
    super(props);
    this.state = {squares: Array(9).fill(null),
      xIsNext: true,}
    }
// HandleClicking of each square
  handleClick(i) {
    const squares = this.state.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
   return;
      }
      // Define what each box-click item will be
    squares[i] = this.state.xIsNext ? 'X' : 'O';
    this.setState({
        squares: squares,
        xIsNext: !this.state.xIsNext
      });
  }
//Render the 'X' or 'O' onClick
  renderSquare(i) {
    return(
     <Square
      value={this.state.squares[i]}
      onClick={() => this.handleClick(i)}
    />
  );
}
//onClick for a New Game
  newGame(){
    this.setState ({
      squares: Array(9).fill(null),
        xIsNext: true,
    })
  };
//Define winner
  render() {
    const winner = calculateWinner(this.state.squares);
    let status;
    if (winner) {
      status = 'Winner: ' + winner;
    } else {
      status = 'Your Turn: ' + (this.state.xIsNext ? 'X' : 'O');
    }
    //<-------Return BEGINS----->
    return (
      <div>
        <div className="title">Tic-Tac-Toe!</div>
        <div className="status">{status}</div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
        <label
          onClick={this.newGame.bind(this)}
          className="newGame btn btn-primary"
          >
            New Game
        </label>
      </div>
    );
  }
}
// <----Return ENDS----->

//Parent component
class Game extends React.Component {
  render() {
    return (
      <div>
        <div className="game-board">
          <Board />
        </div>
        <div className="game-info">
          <div>{/* status */}</div>
          <ol>{/* TODO */}</ol>
        </div>
      </div>
    );
  }
}
//Determining winner from Board clicks
function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}


// ========================================

//REACT RENDER (App.js equivalent)
ReactDOM.render(
  <Game/>,
  document.getElementById('root')
);



